/* Desktop View */
.loginBtn{
    background-color: whitesmoke;
    border-radius: 28px;
    color: black;
    padding: 10px 26px;
    border: 3px solid #343a40eb;
  }
  .desktopMenu {
      display: block;
  }
  div.banner .mobileMenu {
      display: none;
  }
  .navbar{
      width: 100%;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content:  space-between;
    }
    .logo{
        width:  120px;
        cursor: pointer;
        margin-left:50px;
        width: 120px;
        cursor: pointer;
        margin-left: 50px;
        height: 100px;
        width: 200px;
    }
    .navbar ul li{
        list-style: none;
        display: inline-block;
        padding: 0px 20px;
        position: relative;
        cursor: pointer;
    }
    .navbar ul li a{
        text-decoration: none;
        color: black;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
    }
    .navbar ul li::after {
        content: '';
        height:  3px;
        width: 0%;
        background: #009688;
        position: absolute;
        left: 0;
        bottom: -10px;
        transition: 0.5s;
        cursor: pointer;
    }
    .navbar ul li:hover::after {
        width: 100%;
    }
    .mobileSearch{
        display: none;
    }

/* Tab View - 769px */
@media only screen and (min-width: 765px) and (max-width: 980px) {
    .navbar ul li {
        padding: 0px 2px;
        font-style: normal;
        font-weight: 500;
        font-size: 10px;
        line-height: 13px;
        letter-spacing: 0.16em;
      }
      .logo {
        width: 120px;
        cursor: pointer;
        margin-left: 0px;
        height: 100px;
        width: 120px;
        margin-top: -20px;
      }
      .desktopMenu{
        padding-left: 0px;
      }
      .navbar ul li a{
        font-size: 12px;
      }
    }

/* Mobile View - 376px */
      @media only screen and (max-width: 767px){
        .navbar ul li{
            margin: 20px 0px;
        }
        li.nav-item{
          margin-left: 120px;
        }
        .navbar-toggler{
          float: right;
        }
        span.close {
          display: none;
        }
        button[aria-expanded="true"] > span.close {
          display: inline;
          color: #fff;
          opacity: 1;
        }
      
        button[aria-expanded="true"] > span.navbar-toggler-icon {
          display: none;
        }
      
        button[aria-expanded="false"] > span.close {
          display: none;
        }
         .hamburger{
          display: block;
         }
        .desktopMenu {
          display: none;
        }
        div.banner .mobileMenu {
          display: block;
        }
        div.banner .mobileMenu li:hover {
          background-color: #80808052;
        }
        hr{
          margin-top: 10px;
          margin-bottom: 0px;
          border-top: 2px solid white;
        }
        .mobileSearch{
          display: block;
          width: 80%;
          margin-left: 35px;
          margin-top:15px;
        }
        .mobileSearchBtn{
          position: absolute;
          top: 0px;
          left: 280px;
        }
      }